// app/models/create/page.tsx
"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { v4 as uuidv4 } from "uuid";
import { addDoc, collection, doc, setDoc } from "firebase/firestore";
import { db, auth } from "../../firebaseConfig.js";

type EducationModel = {
  id: string;
  name: string;
  philosophy: string;
  evaluationFocus: string;
  languageFocus: string;
  childFocus: string;
  updatedAt: string;
};

type EducationHistory = EducationModel & {
  note: string;
};

export default function CreateModelPage() {
  const router = useRouter();

  const [models, setModels] = useState<EducationModel[]>([]);
  const [form, setForm] = useState({
    name: "",
    philosophy: "",
    evaluationFocus: "",
    languageFocus: "",
    childFocus: "",
    note: "",
  });
  const [editId, setEditId] = useState<string | null>(null);
  const [error, setError] = useState("");

  // localStorage „Åã„ÇâÊó¢Â≠ò„É¢„Éá„É´„ÇíË™≠„ÅøËæº„Åø
  useEffect(() => {
    const stored = localStorage.getItem("styleModels");
    if (stored) setModels(JSON.parse(stored));
  }, []);

  const handleChange = (field: keyof typeof form, value: string) => {
    setForm(prev => ({ ...prev, [field]: value }));
  };

  const handleSave = async () => {
    setError("");
    // ÂøÖÈ†àÈ†ÖÁõÆ„ÉÅ„Çß„ÉÉ„ÇØ
    if (
      !form.name.trim() ||
      !form.philosophy.trim() ||
      !form.evaluationFocus.trim() ||
      !form.languageFocus.trim() ||
      !form.childFocus.trim()
    ) {
      setError("„Åô„Åπ„Å¶„ÅÆÂøÖÈ†àÈ†ÖÁõÆ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ");
      return;
    }

    const now = new Date().toISOString();
    let updatedModels: EducationModel[];

    if (editId) {
      // Á∑®ÈõÜ
      updatedModels = models.map(m =>
        m.id === editId
          ? { ...m, ...form, updatedAt: now }
          : m
      );
    } else {
      // Êñ∞Ë¶è
      updatedModels = [
        {
          id: uuidv4(),
          name: form.name.trim(),
          philosophy: form.philosophy.trim(),
          evaluationFocus: form.evaluationFocus.trim(),
          languageFocus: form.languageFocus.trim(),
          childFocus: form.childFocus.trim(),
          updatedAt: now,
        },
        ...models,
      ];
    }

    // 1) localStorage „Å´‰øùÂ≠ò
    localStorage.setItem("styleModels", JSON.stringify(updatedModels));
    setModels(updatedModels);

    // 2) Firestore „ÅÆ education_models „Ç≥„É¨„ÇØ„Ç∑„Éß„É≥„Å∏‰øùÂ≠ò
    try {
      if (editId) {
        // Êó¢Â≠ò„Éâ„Ç≠„É•„É°„É≥„Éà„Çí‰∏äÊõ∏„Åç
        await setDoc(doc(db, "education_models", editId), {
          id: editId,
          name: form.name.trim(),
          philosophy: form.philosophy.trim(),
          evaluationFocus: form.evaluationFocus.trim(),
          languageFocus: form.languageFocus.trim(),
          childFocus: form.childFocus.trim(),
          updatedAt: now,
          updatedBy: auth.currentUser?.uid || null,
        });
      } else {
        // Êñ∞Ë¶è„Éâ„Ç≠„É•„É°„É≥„Éà‰ΩúÊàê
        await addDoc(collection(db, "education_models"), {
          id: updatedModels[0].id,
          name: form.name.trim(),
          philosophy: form.philosophy.trim(),
          evaluationFocus: form.evaluationFocus.trim(),
          languageFocus: form.languageFocus.trim(),
          childFocus: form.childFocus.trim(),
          updatedAt: now,
          createdBy: auth.currentUser?.uid || null,
        });
      }
      alert("‚úÖ Firestore „Å´„É¢„Éá„É´ÊÉÖÂ†±„Çí‰øùÂ≠ò„Åó„Åæ„Åó„ÅüÔºÅ");
    } catch (e) {
      console.error("Firestore ‰øùÂ≠ò„Ç®„É©„Éº", e);
      alert("‚ö†Ô∏è Firestore „Å∏„ÅÆ‰øùÂ≠ò„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
    }

    // 3) Â±•Ê≠¥„Éá„Éº„Çø„Çí localStorage „Å´ËøΩÂä†
    const newHistoryEntry: EducationHistory = {
      id: editId || updatedModels[0].id,
      name: form.name.trim(),
      philosophy: form.philosophy.trim(),
      evaluationFocus: form.evaluationFocus.trim(),
      languageFocus: form.languageFocus.trim(),
      childFocus: form.childFocus.trim(),
      updatedAt: now,
      note: form.note.trim() || "ÔºàÊõ¥Êñ∞ÊôÇ„Å´„É°„É¢„Å™„ÅóÔºâ",
    };
    const prevHistory = JSON.parse(
      localStorage.getItem("educationStylesHistory") || "[]"
    ) as EducationHistory[];
    const updatedHistory = [newHistoryEntry, ...prevHistory];
    localStorage.setItem(
      "educationStylesHistory",
      JSON.stringify(updatedHistory)
    );

    // 4) Firestore „ÅÆ education_styles_history „Ç≥„É¨„ÇØ„Ç∑„Éß„É≥„Å∏Â±•Ê≠¥‰øùÂ≠ò
    try {
      await addDoc(collection(db, "education_styles_history"), {
        ...newHistoryEntry,
        recordedBy: auth.currentUser?.uid || null,
        recordedAt: now,
      });
      alert("‚úÖ Firestore „Å´Â±•Ê≠¥„ÇíË®òÈå≤„Åó„Åæ„Åó„ÅüÔºÅ");
    } catch (e) {
      console.error("Firestore Â±•Ê≠¥‰øùÂ≠ò„Ç®„É©„Éº", e);
      alert("‚ö†Ô∏è Firestore „Å∏„ÅÆÂ±•Ê≠¥‰øùÂ≠ò„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
    }

    // ÂÆå‰∫Ü„Åó„Åü„Çâ **Â±•Ê≠¥„Éö„Éº„Ç∏** „Å∏Êàª„Çã„Çà„ÅÜ„Å´Â§âÊõ¥
    router.push("/models/history");
  };

  return (
    <main
      style={{
        padding: "2rem 4rem",
        width: "100%",
        maxWidth: 1200,
        margin: "0 auto",
        fontFamily: "sans-serif",
      }}
    >
      {/* „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥ */}
      <nav
        style={{
          display: "flex",
          gap: 12,
          marginBottom: 24,
          overflowX: "auto",
        }}
      >
        {[
          ["/", "üè† „Éõ„Éº„É†"],
          ["/plan", "üìã ÊéàÊ•≠‰ΩúÊàê"],
          ["/plan/history", "üìñ Ë®àÁîªÂ±•Ê≠¥"],
          ["/practice/history", "üì∑ ÂÆüË∑µÂ±•Ê≠¥"],
          ["/models/create", "‚úèÔ∏è ÊïôËÇ≤Ë¶≥‰ΩúÊàê"],
          ["/models", "üìö ÊïôËÇ≤Ë¶≥‰∏ÄË¶ß"],
          ["/models/history", "üïí ÊïôËÇ≤Ë¶≥Â±•Ê≠¥"],
        ].map(([href, label]) => (
          <Link
            key={href}
            href={href}
            style={{
              padding: "8px 12px",
              backgroundColor:
                href === "/models/create" ? "#4CAF50" : "#1976d2",
              color: "white",
              borderRadius: 6,
              textDecoration: "none",
              whiteSpace: "nowrap",
            }}
          >
            {label}
          </Link>
        ))}
      </nav>

      <h1
        style={{
          fontSize: "2rem",
          marginBottom: "1.5rem",
          textAlign: "center",
        }}
      >
        {editId ? "‚úèÔ∏è ÊïôËÇ≤Ë¶≥„É¢„Éá„É´„ÇíÁ∑®ÈõÜ" : "‚úèÔ∏è Êñ∞„Åó„ÅÑÊïôËÇ≤Ë¶≥„É¢„Éá„É´„Çí‰ΩúÊàê"}
      </h1>

      {error && (
        <p
          style={{
            color: "red",
            marginBottom: "1rem",
            textAlign: "center",
          }}
        >
          {error}
        </p>
      )}

      <section
        style={{
          backgroundColor: "#f9f9f9",
          padding: 24,
          borderRadius: 8,
          boxShadow: "0 2px 6px rgba(0,0,0,0.1)",
        }}
      >
        <label style={{ display: "block", marginBottom: 12 }}>
          „É¢„Éá„É´ÂêçÔºàÂøÖÈ†àÔºâÔºö
          <input
            type="text"
            value={form.name}
            onChange={e => handleChange("name", e.target.value)}
            style={{
              width: "100%",
              padding: "0.8rem",
              fontSize: "1.1rem",
              borderRadius: 6,
              border: "1px solid #ccc",
              marginTop: 4,
            }}
          />
        </label>

        <label style={{ display: "block", marginBottom: 12 }}>
          ÊïôËÇ≤Ë¶≥ÔºàÂøÖÈ†àÔºâÔºö
          <textarea
            rows={2}
            value={form.philosophy}
            onChange={e => handleChange("philosophy", e.target.value)}
            style={{
              width: "100%",
              padding: "0.8rem",
              fontSize: "1.1rem",
              borderRadius: 6,
              border: "1px solid #ccc",
              marginTop: 4,
            }}
          />
        </label>

        <label style={{ display: "block", marginBottom: 12 }}>
          Ë©ï‰æ°Ë¶≥ÁÇπ„ÅÆÈáçË¶ñÁÇπÔºàÂøÖÈ†àÔºâÔºö
          <textarea
            rows={2}
            value={form.evaluationFocus}
            onChange={e => handleChange("evaluationFocus", e.target.value)}
            style={{
              width: "100%",
              padding: "0.8rem",
              fontSize: "1.1rem",
              borderRadius: 6,
              border: "1px solid #ccc",
              marginTop: 4,
            }}
          />
        </label>

        <label style={{ display: "block", marginBottom: 12 }}>
          Ë®ÄË™ûÊ¥ªÂãï„ÅÆÈáçË¶ñÁÇπÔºàÂøÖÈ†àÔºâÔºö
          <textarea
            rows={2}
            value={form.languageFocus}
            onChange={e => handleChange("languageFocus", e.target.value)}
            style={{
              width: "100%",
              padding: "0.8rem",
              fontSize: "1.1rem",
              borderRadius: 6,
              border: "1px solid #ccc",
              marginTop: 4,
            }}
          />
        </label>

        <label style={{ display: "block", marginBottom: 12 }}>
          ËÇ≤„Å¶„Åü„ÅÑÂ≠ê„Å©„ÇÇ„ÅÆÂßøÔºàÂøÖÈ†àÔºâÔºö
          <textarea
            rows={2}
            value={form.childFocus}
            onChange={e => handleChange("childFocus", e.target.value)}
            style={{
              width: "100%",
              padding: "0.8rem",
              fontSize: "1.1rem",
              borderRadius: 6,
              border: "1px solid #ccc",
              marginTop: 4,
            }}
          />
        </label>

        <label style={{ display: "block", marginBottom: 24 }}>
          Êõ¥Êñ∞„É°„É¢Ôºà‰ªªÊÑèÔºâÔºö
          <textarea
            rows={2}
            value={form.note}
            onChange={e => handleChange("note", e.target.value)}
            style={{
              width: "100%",
              padding: "0.8rem",
              fontSize: "1.1rem",
              borderRadius: 6,
              border: "1px solid #ccc",
              marginTop: 4,
              fontStyle: "italic",
            }}
          />
        </label>

        <div style={{ textAlign: "center" }}>
          <button
            onClick={handleSave}
            style={{
              padding: "0.8rem 2rem",
              fontSize: "1.1rem",
              backgroundColor: "#4CAF50",
              color: "white",
              border: "none",
              borderRadius: 8,
              cursor: "pointer",
            }}
          >
            {editId ? "Êõ¥Êñ∞„Åó„Å¶‰øùÂ≠ò" : "‰ΩúÊàê„Åó„Å¶‰øùÂ≠ò"}
          </button>
        </div>
      </section>
    </main>
  );
}
